import React, {Component} from 'react';
import { ProgressBar } from 'react-bootstrap';

class Timer extends Component {
    constructor(props) {
        super(props);
        this.state = {
          initTimer: 10,
          timer: 10,
        };
      }
    componentDidMount() {
        this.interval = setInterval(() => this.setState({ timer: this.state.timer - 1 }), 1000);
    }
    componentWillUnmount() {
        clearInterval(this.interval);
    }
    componentDidUpdate() {
        if(this.state.timer === 0){
            // Out of time
            console.log("No time");
            this.props.handleTimerEnd();
            this.setState({ timer: 10 });   
        }
    }
    handleTimerChange = (newTimer) => {
        this.props.handleTimerChange(newTimer)
    };

    render() {
        var animation = "danger";
        if(this.state.timer > (this.state.initTimer / 2)){animation = "success";}
        else if(this.state.timer > (this.state.initTimer) / 4){animation = "warning";}
        return (
            <ProgressBar animated variant={animation} now={(this.state.timer) * 10} label={`${this.state.timer}`}/>
        );
        
    }
}
   
export default Timer;

